import React from "react";
import "./SkillCard.css";

const SkillCard = (props) => {
  console.log(props);
  return (
    <div className="skillCardContainer">
      <h2>{props.h2head}</h2>
      <div className="skillList">
        {props.skills.map((sk) => {
          return (
            <div className="skill">
              <img src={sk.img} />
              <p>{sk.skillName}</p>
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default SkillCard;
